@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');

.box {
    border-radius: 1em;
    background-color: azure;
    padding: 20px;
    margin: 10px;
}

.bottom-box {
    margin-bottom: 20px;
}

.innerbox > form {
    margin-left: 20vh;
    margin-right: 20vh;
    display: flex;
    flex-direction: column;
}

.innerbox > form > button {
    margin-top: 10px;
    border-radius: 1em;
    background-color: slategray;
    border: none;
    padding: 10px;
    color: azure;
    font-weight: bold;
    font-family: 'Lato', sans-serif;
}

.innerbox > form > button:hover {
    background-color: rgb(151, 162, 173);
}

@media screen and (max-width: 768px){
    /* place here CSS targeted at iPads and smaller, including iPhones and small hand-held devices */

    .innerbox > form {
        margin-left: 5px;
        margin-right: 5px;
    }
}