@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-Container {
  margin-top: 5px;
  margin-left: 30vh;
  margin-right: 30vh;
  background-color: slategray;
  padding: 20px;
  border-radius: 1em;
}

.App-Container > h1 {
  margin-top: 0pxs;
  color: azure;
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}

@media screen and (max-width: 768px){
  /* place here CSS targeted at iPads and smaller, including iPhones and small hand-held devices */

  .App-Container {
    margin-left: 5px;
    margin-right: 5px;
  }
}

.Restaurant-Component {
    background-color: azure;
    border-radius: 1em;
    font-family: 'Lato', sans-serif;
    display: flex;
    align-items: center;
    padding: 10px;
    margin-bottom: 20px;
    width: calc(100% - 20px);
    text-decoration: none;
}

.Restaurant-Component:hover {
    background-color: white;
}

.Restaurant-Component > h3 {
    margin-left: auto;
    border-radius: 1em;
    background-color: slategray;
    color: azure;
    padding: 15px;
}

.Restaurant-Component > h2 {
    color: black;
}

.Restaurant-Component > h2 > span {
    font-style: italic;
}

.Restaurant-Phone {
    font-style: italic;
}

.backButton {
    text-decoration: none;
    border-radius: 1em;
    background-color: azure;
    color: black;
    padding: 10px;
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    margin-left: 10px;
    margin-right: 10px;
}

.backButton:hover {
    background-color: white;
}
.spinner {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
.box {
    border-radius: 1em;
    background-color: azure;
    padding: 20px;
    margin: 10px;
}

.bottom-box {
    margin-bottom: 20px;
}

.innerbox > form {
    margin-left: 20vh;
    margin-right: 20vh;
    display: flex;
    flex-direction: column;
}

.innerbox > form > button {
    margin-top: 10px;
    border-radius: 1em;
    background-color: slategray;
    border: none;
    padding: 10px;
    color: azure;
    font-weight: bold;
    font-family: 'Lato', sans-serif;
}

.innerbox > form > button:hover {
    background-color: rgb(151, 162, 173);
}

@media screen and (max-width: 768px){
    /* place here CSS targeted at iPads and smaller, including iPhones and small hand-held devices */

    .innerbox > form {
        margin-left: 5px;
        margin-right: 5px;
    }
}
