@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');

.Restaurant-Component {
    background-color: azure;
    border-radius: 1em;
    font-family: 'Lato', sans-serif;
    display: flex;
    align-items: center;
    padding: 10px;
    margin-bottom: 20px;
    width: calc(100% - 20px);
    text-decoration: none;
}

.Restaurant-Component:hover {
    background-color: white;
}

.Restaurant-Component > h3 {
    margin-left: auto;
    border-radius: 1em;
    background-color: slategray;
    color: azure;
    padding: 15px;
}

.Restaurant-Component > h2 {
    color: black;
}

.Restaurant-Component > h2 > span {
    font-style: italic;
}

.Restaurant-Phone {
    font-style: italic;
}

.backButton {
    text-decoration: none;
    border-radius: 1em;
    background-color: azure;
    color: black;
    padding: 10px;
    font-family: 'Lato', sans-serif;
    font-weight: bold;
    margin-left: 10px;
    margin-right: 10px;
}

.backButton:hover {
    background-color: white;
}